@import "utils/styles/variables";


.formCheckbox__Container {
    margin-top:  1rem;
    display: flex;
    flex-direction: column;
    position: relative;
    font-size: 1.3rem;
}
.formCheckbox__Wrapper {
    display: flex;
    align-items: center;
}

.formCheckbox__Label {
    color: map-get($collors, "text");
    font-size: .9rem;
    font-weight: 500;
    padding-left: 1rem;
}

.fomrCheckbox__Input {
    height: 1.3rem;
    width: 1.3em;
    border: 1px solid map-get($collors, "border");
}

.formCheckbox__Error {
    color: map-get($collors, "delete");
    font-weight: bold;
    font-size: 0.8rem;
    margin: 0.5rem 0;
    min-height: 1rem;
}