@import "utils/styles/variables";

.relativeCheckbox__Container {
    margin-bottom: 1.5rem;
    
}

.relativeCheckbox__Wrapper {
    display: flex;
    width: 100%;
    min-height: 5rem;
    justify-content: space-between;
}

.relativeCheckbox__Error {
    display: block;
    width: 100%;
    color: map-get($collors, "delete");
    font-weight: bold;
    font-size: 0.8rem;
    margin: 0.5rem 0;
    min-height: 1rem;
}