@import "utils/styles/variables";

.tabs__Container {
    width: 1000px;
    max-width: 95%;
    z-index: 2;
}

.tabs__Nav {
    width: 100%;
    display: flex;
    align-items: flex-end;
}

.tab__Wrapper {
    display: grid;
    height: 80vh;
    grid-template-rows: min-content min-content 1fr min-content;
    padding: 0;
}

