@import "utils/styles/variables";

.formInput__Wrapper {
    margin: 1rem 0;
    display: flex; 
    flex-direction: column;
    position: relative;
    font-size: 1.3rem;

    &--number {
        margin-bottom: 0;
    }

    &:nth-last-child(2) {
        margin-bottom: 0;
    }
}

.formInput__Label {
    
    position: absolute;
    top: 0;
    left: .5rem;
    color: map-get($collors, "text");
    font-size: 0.8rem;
    font-weight: 500;
    transform: translateY(-50%);
    background-color: map-get($collors, "component-bg");
    padding: 0 .5rem;
}

.fomrInput__Input {
    border: solid 1px map-get($collors, "border");
    padding: 0.6rem 0.5rem;
    border-radius: 5px;
    font-size: 1.1rem;
    background-color: map-get($collors, "component-bg");

    &:hover {
        border-color: map-get($collors, "primary");
    }

    &:focus {
        outline: 2px solid map-get($collors, "primary");
    }

    &:disabled {
        background-color:  map-get($collors, "component-bg-2");

        &:hover {
            border: solid 1px map-get($collors, "border");
        }

        &:focus {
            outline: 0;
        }
    }
}

:local(.fomrInput__Input):focus + :local(.formInput__Label) {
    color: map-get($collors, "primary");
}

:local(.fomrInput__Input):disabled + :local(.formInput__Label) {
    color: map-get($collors, "text");
    background-color: rgba(map-get($collors, "component-bg"), 0.5);
    font-weight: 400;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px map-get($collors, "component-bg") inset !important;
    box-shadow: 0 0 0 30px map-get($collors, "component-bg") inset !important;

}

.formInput__Error {
    display: block;
    width: 100%;
    color: map-get($collors, "delete");
    font-weight: bold;
    font-size: 0.8rem;
    margin: 0.5rem 0;
    min-height: 1rem;
}